.experience{
  display: flex;
  gap: 0 70px;
  margin-top: 50px;
  @media (max-width: 700px) {
    margin-top: 20px;
    flex-direction: column;
    gap: 15px 0;
  }
}
.list {
  flex: 1 1;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 20px 0;
  border-radius: 10px;
  background:  #16181c;
  padding: 30px;
  min-height: 180px;
  @media (max-width: 700px) {
    padding: 15px;
  }

}

.place {
  font-size: 22px;
}

.year {
  color: #ff014f;
}

.speciality {
  line-height: 22px;
  font-size: 18px;
  color:#878e99;
}