.header {
  min-height: 80px;
  width: 100%;
  z-index: 1000;
  position: fixed;
  padding: 10px 150px 10px 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #212428de;
  box-shadow: 10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
  @media (max-width:1200px ) {
    padding: 10px 50px 10px 50px;
  }
  @media (max-width:600px ) {
    padding: 10px 20px 10px 20px;
  }
  @media (max-width:400px ) {
    padding: 10px 10px 10px 10px;
  }
}

.headerLogo {
  display: flex;
  align-items: center;
  gap: 20px;
}

.logoImg {
  width: 60px;
  height: 60px;
  @media (max-width:750px ) {
    width: 30px;
    height: 30px;
  }
  @media (max-width:400px ) {
    width: 25px;
    height: 25px;
  }
}

.logoText {
  color: #ffffff;
  font-weight: 700;
  font-size: 24px;
  line-height: 62px;
  @media (max-width:750px ) {
    display: none;
  }

}

.nav {

}

.items {
  display: flex;
  gap: 0 30px;
  color: #abb8c3;
  @media (max-width:520px ) {
    gap: 0 15px;
  }
  @media (max-width:430px ) {
    gap: 0 12px;
  }
  @media (max-width:430px ) {
    gap: 0 10px;
  }
}

.item {
  text-transform: uppercase;
  @media (max-width:520px ) {
    font-size: 14px;
  }
  @media (max-width:430px ) {
    font-size: 12px;
  }
  @media (max-width:360px ) {
    font-size: 10px;
  }
}

.link {

  color: #c4cfde;

  :hover {
    color: #f9004d;

  }
}